export default {
	data() {
		return {
			Privacy:false
		}
	},
	onLoad: function() {
		if(uni.getStorageSync('Privacy_policy') == 0){
			this.Privacy = true
		}else if(uni.getStorageSync('Privacy_policy') == 1){
			uni.switchTab({
			    url: '../tabBar/signUp/signUp'
			});
		}
	},
	methods: {
		tapIn: function(e) {
			uni.setStorageSync('Privacy_policy', 1)
			uni.switchTab({
				url: '../tabBar/signUp/signUp'
			});
		},
		tapOut: function(e) {














		}
	}
}
